<template>
  <li class="product-list-item">
    <div class="product-list-item-pic">
      <img :src="img" />
    </div>
    <div class="product-list-item-text">
      <h5 class="product-title">{{ title }}</h5>
      <h6 class="product-price">${{ price }}</h6>
    </div>
    <div class="quantity">
      <ButtonQuantity @inputQuantity="GetInputQuantity" />
    </div>
    <div class="btn-area">
      <button class="btn add-to-cart" @click="AddToCart">加入購物車</button>
    </div>
  </li>
</template>

<script>
import ButtonQuantity from '@/components/ButtonQuantity'

export default {
  name: 'ProductListItem',
  components: {
    ButtonQuantity,
  },
  props: {
    id: Number,
  },
  data() {
    return {
      title: '',
      type: '',
      price: 0,
      img: '',
      quantity: 1,
    }
  },
  methods: {
    GetInputQuantity(input) {
      this.quantity = input
    },
    AddToCart() {
      let item = {
        id: this.id,
        quantity: this.quantity,
      }
      this.$store.dispatch('cart/ItemAdd', item)
    },
  },
  mounted() {
    const { title, type, price, img } = this.$store.getters['products/ProductInfo'](this.id)
    this.title = title
    this.type = type
    this.price = price
    this.img = require(`../assets/${img}`)
  },
}
</script>

<style lang="scss">
.product-list-item {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;

  @include md {
    width: 50%;
  }

  @include lg {
    width: 25%;
  }

  .product-list-item-pic {
    overflow: hidden;

    img {
      transform: scale(1);
      transition: all 0.5s ease-in-out;

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  .product-list-item-text {
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }

  .quantity {
    margin: 5px 0;
  }

  .btn-area {
    text-align: center;
  }
}
</style>
