<template>
  <div>
    <h4 class="list-title">商品列表</h4>
    <ol class="type-list">
      <li class="type-list-item" @click="ChangeType('all')"><span>全部</span></li>
      <li class="type-list-item" @click="ChangeType('food')"><span>主餐</span></li>
      <li class="type-list-item" @click="ChangeType('snack')"><span>點心</span></li>
      <li class="type-list-item" @click="ChangeType('drink')"><span>飲料</span></li>
    </ol>
    <ol class="product-list" v-if="ProductList.length > 0">
      <ProductListItem v-for="product in ProductList" :id="product.id" :quantity="1" :key="product.id" />
    </ol>
    <div class="nothing-in-list" v-if="ProductList.length <= 0">
      <h6>不好意思，該品項目前沒有商品喔！</h6>
    </div>
  </div>
</template>

<script>
import ProductListItem from '@/components/ProductListItem.vue'

export default {
  name: 'ProductList',
  components: {
    ProductListItem,
  },
  data() {
    return {
      productList: [],
      type: '',
    }
  },
  computed: {
    ProductList() {
      return this.$store.getters['products/FilterList'](this.type)
    },
  },
  methods: {
    ChangeType(type) {
      this.type = type
    },
  },
  created() {
    this.type = 'all'
  },
}
</script>

<style lang="scss">
.list-title {
  text-align: center;
}

.type-list {
  @include flex();
  .type-list-item {
    padding: 5px 10px;
    margin: 0px 5px;
    border-radius: 5px;
    background-color: $color-lightGray;
    cursor: pointer;
  }
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
}

.nothing-in-list {
  margin: 30px 0;
  text-align: center;
}
</style>
